#statTitle {
    text-align: center;
}
.container {
    width: 30vw;
}
.statsDropdown {
    background-color: #212529fd;
    min-width: 60vw;
    color: white;
    padding: 0;
    border-radius: 0.5em;
    text-align: center;
    border: 1px solid #585b5e85; 
}
.col{
    margin: 5px;
    font-size: 1em;
    animation: opacity 2s;
    animation-iteration-count: initial;
}
.teamType {
    font-size: 2em;
    padding: 0;
    border-bottom: 1px #585B5E solid;
}
.statsBtn {
    font-weight: bold;
    font-size: 1.5em;
    margin-left: 50px;
}
@media (max-width: 1120px) {
.col{
    font-size: 1em;
}
}
@media (max-width: 750px) {
    .statsBtn {
        font-size: 1em;
    }
}