@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.form-control:focus {
  border-color: #3d3a3a;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(37, 37, 37, 0.6);
}
#form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: fixed;
  width: 100%;
  min-height: 100%;
  height: auto;
  top:0px;
  left:0px;
  color: white;
}
.form-label {
  font-size: 1.5em;
}
.title {
  font-size: 5em;
}
.alert-danger{
  background-color: rgba(139, 60, 60);
  text-align: center;
  border: none;
  color: white;
  font-size: 1.5em;
}
.loginAsGuest {
  position: absolute;
  left: 2vw;
  bottom: 2vw;
}
#title {
    font-family: sans-serif;
    font-size: 3em;
    font-weight: bold;
    color: white;
}
a {
    text-decoration: none;
}
#icon {
    width: 150px;
    height: 150px;
    color: white;
}
.salir {
    font-size: 1.5em;
    font-weight: bold;
}
@media (max-width: 620px) {
    #title {
        font-size: 1em;
    }
    .salir {
        font-size: 1em;
    }
}
.searchContainer {
    margin-left: auto;
    margin-right: auto;
    width: 50vw;
    display: flex;
    justify-content: center;
    font-size: 2.5em;
    border-radius: 3em;
    flex-direction: row;
}
.heroCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 1px solid white;
}
.alert-success{
    background-color: rgba(33, 78, 48);
    text-align: center;
    border: none;
    color: white;
    font-size: 1.5em;
}
.searchResults {
    margin-top: 5vh;
    margin-left: auto;
    margin-right: auto;
    background-color: #212529fd;
    width: 90vw;
    display: flex;
    border-radius: 3em;
    justify-content: center;
    flex-wrap: wrap;
}
.alertSearch{
    position: fixed;
    width: 100vw;
    top: 0;
}
@media (max-width: 1050px) {
    .addSuperHero{
        font-size: 1em;
        text-align: center;
    }
    .searchResults {
        width: auto;
    }
}
@media (max-width: 400px) {
    .addSuperHero{
        font-size: 0.5em;
    }
}
#statTitle {
    text-align: center;
}
.container {
    width: 30vw;
}
.statsDropdown {
    background-color: #212529fd;
    min-width: 60vw;
    color: white;
    padding: 0;
    border-radius: 0.5em;
    text-align: center;
    border: 1px solid #585b5e85; 
}
.col{
    margin: 5px;
    font-size: 1em;
    -webkit-animation: opacity 2s;
            animation: opacity 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-iteration-count: initial;
            animation-iteration-count: initial;
}
.teamType {
    font-size: 2em;
    padding: 0;
    border-bottom: 1px #585B5E solid;
}
.statsBtn {
    font-weight: bold;
    font-size: 1.5em;
    margin-left: 50px;
}
@media (max-width: 1120px) {
.col{
    font-size: 1em;
}
}
@media (max-width: 750px) {
    .statsBtn {
        font-size: 1em;
    }
}
.powerStatDropdown{
    background-color: #14171ae0;
    width: 280px;
    padding: 0;
    border-radius: 0.5em;
    position: absolute;
    inset: auto auto 0px 0px!important;
    margin: 0px;
    -webkit-transform: translate(0px, -40px)!important;
            transform: translate(0px, -40px)!important;
}
.powerstat{
    text-align: start;
    color: white;
    margin: 5px;
    font-size: 1.5em;
}
.progress-bar {
    overflow: visible;
    margin-bottom: 2px;
    background-color: rgba(77, 77, 114);
    border-radius: 0.1em;
    -webkit-animation: opacity 2s;
            animation: opacity 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-iteration-count: initial;
            animation-iteration-count: initial;
}
  
  @-webkit-keyframes opacity {
    0% {
        opacity: 0;
    }
      100% {
        opacity: 1;
      }
}
  
  @keyframes opacity {
    0% {
        opacity: 0;
    }
      100% {
        opacity: 1;
      }
}

#team {
    display: flex;
    flex-wrap: wrap;
    min-height: 20vh;
    justify-content: center;
}
.teamMember {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin: 1vw;
    width: 350px;
}
.teamMemberImg {
    border-radius: 2em;
    height: 465px;
}
.heroNameContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 20%;
    width: 100%;
    background-color: #212529ce;
}
.heroName {
    font-size: 2em;
    font-weight: bold;
    margin: 0;
}
.buttons {
    display: flex;
    position: relative;
    justify-content: space-evenly;
    bottom: 10%;
}
.btnTeam{
    font-weight: bold;
    background-color: #212529ce;
}

#noTeam {
    font-size: 3em;
    margin-top: 4vh;
    -webkit-animation: MoveUpDown 5s linear infinite;
            animation: MoveUpDown 5s linear infinite;
}
  
  @-webkit-keyframes MoveUpDown {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
      }
    
      50% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
      }
    
      100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
      }
}
  
  @keyframes MoveUpDown {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
      }
    
      50% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
      }
    
      100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
      }
}
@media (max-width: 620px) {
    #team {
        min-height: 7vh;
    }
    #noTeam {
        margin: 0;
        font-size: 1.5em;
    }
    body{
        overflow-x: hidden
    }
}
#home {
    margin-left: auto;
    margin-right: auto;
    background-color: #212529fd;
    width: 90vw;
    display: flex;
    border-radius: 3em;
    align-self: center;
    flex-direction: column;
    align-items: stretch;
}
#header {
    display:flex;
    margin: 1vw;
    justify-content:space-between;
    align-items: center;
    border-bottom: 1px solid #585B5E; 
}
#titleContainer {
    display: flex;
    align-items: center;
}
#teamTitle {
    margin-left: 50px;
    font-size: 3em;
}
.addHero{
    color: #d1d0d0;
    background-color: rgb(33, 37, 70);
    border: 1px solid #585B5E; 
    margin-right: 5vw;
    font-size: 1.5em;
    font-weight: bold;
}
.addHero:hover {
    color: #ffffff;
    font-weight: bold;
    background-color: rgb(45, 50, 94);
}

@media (max-width: 1120px) {
    #home {
        width: auto;
    }
}
@media (max-width: 750px) {
    body{
        overflow-x: hidden
    }
    .addHero{
        font-size: 1em;
    }
}
@media (max-width: 620px) {
    #teamTitle {
        display: none;
    }
}
#detailsCard {
    margin-left: auto;
    margin-right: auto;
    background-color: #212529;
    width: 70vw;
    display: flex;
    border-radius: 3em;
    align-self: center;
    flex-direction: row;
}
#detailsImg {
    max-width: 400px;
    height: 450px;
    border-radius: 3em 0 0 3em;
    border-right: 1px solid #585B5E; 
}
#details {
    padding-left: 2vw;
}
#heroTitle {
    margin: 0;
    padding-top: 1vw;
    padding-left: 2vw;
    font-size: 3em;
    text-align: center;
    font-weight: bold;
}
.info {
    font-size: 1.1em;
}
    .back {
        position: absolute;
        left: 3vw;
        bottom: 3vw;
        font-size: 1.5em;
        font-weight: bold;
    }

.spinner-border{
    width: 15em;
    margin-top: 20vh;
    height: 15em;
    text-align: center;
}
@media (max-width: 1050px) {
    #detailsCard {
        width: 100vw;
    }
}
@media (max-width: 970px) {
    .back {
        bottom: 0vw;
    }
}
@media (max-width: 750px) {
    #detailsImg {
        width:200px;
        height: 200px
    }
}
@media (max-width: 500px) {
    .info {
        font-size: 0.8em;
    }
    #detailsCard {
        margin-top:8vh;
        height: auto;
    }
}
html,body{
  height: 100%;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/challenge-react/static/media/superhero-background.75084a68.jpeg) no-repeat center fixed;
  background-color: rgb(0, 0, 0);     
  color: white;
}

